'use client';

import { useState, useEffect } from 'react';
import { FaChevronLeft, FaChevronRight, FaRegUserCircle, FaPlusCircle, FaTrophy } from 'react-icons/fa';

// --- Type Definitions ---
declare global {
  interface Window {
    ethereum?: any;
  }
}

// --- Main Page Component ---
export default function TetherLuckPage() {
  const [account, setAccount] = useState<string | null>(null);
  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 1,
    seconds: 40,
  });

  const connectWallet = async () => {
    if (typeof window.ethereum !== 'undefined') {
      try {
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        setAccount(accounts[0]);
      } catch (err) {
        console.error('Failed to connect wallet.', err);
      }
    } else {
      alert('Please install MetaMask.');
    }
  };

  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft(prevTime => {
        let { days, hours, minutes, seconds } = prevTime;

        if (seconds > 0) {
          seconds--;
        } else if (minutes > 0) {
          minutes--;
          seconds = 59;
        } else if (hours > 0) {
          hours--;
          minutes = 59;
          seconds = 59;
        } else if (days > 0) {
          days--;
          hours = 23;
          minutes = 59;
          seconds = 59;
        } else {
          clearInterval(timer);
          // Handle timer end
        }
        return { days, hours, minutes, seconds };
      });
    }, 1000);

    return () => clearInterval(timer);
  }, []);
  
  const participationOptions = [
    { amount: 10, color: 'blue', participants: '1,144,999' },
    { amount: 100, color: 'yellow', participants: '1,144,999' },
    { amount: 500, color: 'green', participants: '1,144,999' },
    { amount: 1000, color: 'red', participants: '1,144,999' },
  ];
  
  const colorClasses: { [key: string]: { border: string; shadow: string; bg: string; text:string } } = {
    blue: { border: 'border-blue-500/50', shadow: 'shadow-blue-500/10', bg: 'bg-blue-500', text: 'text-blue-400' },
    yellow: { border: 'border-yellow-500/50', shadow: 'shadow-yellow-500/10', bg: 'bg-yellow-500', text: 'text-yellow-400' },
    green: { border: 'border-green-500/50', shadow: 'shadow-green-500/10', bg: 'bg-green-500', text: 'text-green-400' },
    red: { border: 'border-red-500/50', shadow: 'shadow-red-500/10', bg: 'bg-red-500', text: 'text-red-400' },
  };

  const formatTime = (time: number) => time.toString().padStart(2, '0');

  return (
    <div className="min-h-screen bg-gradient-to-b from-[#1a1a3d] to-[#12122b] text-white font-sans">
      {/* Header */}
      <header className="flex justify-between items-center p-6 px-12">
        <div className="text-2xl font-bold flex items-center gap-2">
            <span className="text-yellow-400"><FaTrophy /></span>
            TetherLuck
        </div>
        <nav className="flex items-center gap-6">
          <a href="#" className="hover:text-yellow-400 transition-colors">참여 방법</a>
          <button 
            onClick={connectWallet}
            className="bg-yellow-400 text-black font-bold py-2 px-6 rounded-full hover:bg-yellow-300 transition-all"
          >
            {account ? `${account.substring(0, 6)}...${account.slice(-4)}` : '지갑 연동'}
          </button>
        </nav>
      </header>

      <main className="px-12 py-4">
        {/* Top Info Bar */}
        <div className="bg-black bg-opacity-30 rounded-full p-2 mb-8 flex justify-between items-center text-sm">
            <div className="flex items-center gap-4">
                <span className="cursor-pointer hover:text-yellow-400"><FaChevronLeft /></span>
                <span>회차 21회</span>
                <span className="text-yellow-400 font-bold">회차 20회</span>
                <span>회차 19회</span>
                <span className="cursor-pointer hover:text-yellow-400"><FaChevronRight /></span>
            </div>
            <div className="flex items-center gap-8">
                <div className="flex items-center gap-2">
                    <span className="cursor-pointer hover:text-yellow-400"><FaChevronLeft size={12} /></span>
                    <span>USDT 1000</span>
                    <span className="cursor-pointer hover:text-yellow-400"><FaChevronRight size={12} /></span>
                </div>
                <span>당첨자 정보</span>
                <div className="flex items-center gap-2 text-yellow-400">
                    <FaTrophy />
                    <span>Winner</span>
                    <span className="font-bold">aswlfjs</span>
                    <span>1,000만원</span>
                </div>
            </div>
        </div>

        {/* Main Content */}
        <div className="text-center relative">
          <h2 className="text-3xl font-bold">20회</h2>
          <p className="text-gray-400 mb-4">2025.07.23</p>
          
          <div className="text-7xl font-mono font-bold my-8 tracking-wider">
            <span>{formatTime(timeLeft.hours)}</span>
            <span className="animate-pulse">:</span>
            <span>{formatTime(timeLeft.minutes)}</span>
            <span className="animate-pulse">:</span>
            <span>{formatTime(timeLeft.seconds)}</span>
          </div>

          <div className="absolute top-0 right-0 flex flex-col gap-4">
              <button className="flex flex-col items-center hover:text-yellow-400 transition-colors">
                  <FaPlusCircle size={32} />
                  <span className="text-xs mt-1">게임설명</span>
              </button>
              <button className="flex flex-col items-center hover:text-yellow-400 transition-colors">
                  <FaRegUserCircle size={32} />
                  <span className="text-xs mt-1">마이페이지</span>
              </button>
          </div>

          {/* Participation Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 mt-12">
            {participationOptions.map(option => {
                const colors = colorClasses[option.color];
                return (
                    <div key={option.amount} className={`bg-gradient-to-b from-[#2a2a57] to-[#1f1f40] border ${colors.border} rounded-2xl p-6 flex flex-col items-center shadow-lg ${colors.shadow}`}>
                        <p className="font-bold text-xl">USDT</p>
                        <p className="font-extrabold text-4xl mb-4">{option.amount}</p>
                        <div className={`relative w-32 h-32 flex items-center justify-center mb-6`}>
                            <div className={`absolute w-full h-full rounded-full ${colors.bg} opacity-20 animate-ping`}></div>
                            <div className={`relative text-6xl font-bold ${colors.text}`}>T</div>
                        </div>
                        <button className="bg-gray-800 w-full py-3 rounded-full font-bold hover:bg-yellow-400 hover:text-black transition-all mb-3">
                        참여하기
                        </button>
                        <p className="text-sm text-gray-400">{option.participants} 명</p>
                    </div>
                )
            })}
          </div>
        </div>
      </main>
    </div>
  );
}
